
.panel {
  display: inline-block;
  height: 100vh;
  border: 1px solid black;
  position: relative;
}

.sidePanel { background-color: goldenrod; }
.sidePanelOpenned { width: 15%; }
.sidePanelClosed { width: 1%; }
.centralPanelShrinked { width: 70%; }
.centralPanelExpandedOneSide { width: 84%; }

@-webkit-keyframes sidePanelClosing { from { width: 15%; } to { width: 1%; } }

@keyframes sidePanelClosing { from { width: 15%; } to { width: 1%; } }
@-webkit-keyframes sidePanelOpenning { from { width: 1%; } to { width: 15%; } }
@keyframes sidePanelOpenning { from { width: 1%; } to { width: 15%; } }
@-webkit-keyframes centralPanelExpandingOneSide { from { width: 70%; } to { width: 84%; } }
@keyframes centralPanelExpandingOneSide { from { width: 70%; } to { width: 84%; } }
@-webkit-keyframes centralPanelShrinkingOneSide { from { width: 84%;} to { width: 70%; } }
@keyframes centralPanelShrinkingOneSide { from { width: 84%;} to { width: 70%; } }
@-webkit-keyframes centralPanelExpandingTwoSides { from { width: 84%; } to { width: 98%; } }
@keyframes centralPanelExpandingTwoSides { from { width: 84%; } to { width: 98%; } }
@-webkit-keyframes centralPanelShrinkingTwoSides { from { width: 98%; } to { width: 84%; } }
@keyframes centralPanelShrinkingTwoSides { from { width: 98%; } to { width: 84%; } }

.sidePanelClosing {
  -webkit-animation-name: sidePanelClosing;
          animation-name: sidePanelClosing;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.sidePanelOpenning {
  -webkit-animation-name: sidePanelOpenning;
          animation-name: sidePanelOpenning;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.centralPanelExpandingOneSide {
  -webkit-animation-name: centralPanelExpandingOneSide;
