.buttonAbs {
  position: absolute;
  top: 40%;
  z-index: 2;
}

.buttonLeftPanelOpen { right: 0; }
.buttonRightPanelOpen { left: 0; }
.buttonLeftPanelClosed { right: -25px; -webkit-transform: rotate(180deg); -ms-transform: rotate(180deg); transform: rotate(180deg);}
.buttonRightPanelClosed { left: -25px; -webkit-transform: rotate(180deg); -ms-transform: rotate(180deg); transform: rotate(180deg);}

@-webkit-keyframes buttonLeftMovingLeft { from { right: 0px; } to { right: -25px; } }

@keyframes buttonLeftMovingLeft { from { right: 0px; } to { right: -25px; } }
@-webkit-keyframes buttonRightMovingRight { from { left: 0; } to { left: -25px; } }
@keyframes buttonRightMovingRight { from { left: 0; } to { left: -25px; } }
@-webkit-keyframes buttonLeftMovingRight { from { right: -25px; } to { right: 0px; } }
@keyframes buttonLeftMovingRight { from { right: -25px; } to { right: 0px; } }
@-webkit-keyframes buttonRightMovingLeft { from { left: -25px; } to { left: 0; } }
@keyframes buttonRightMovingLeft { from { left: -25px; } to { left: 0; } }

@-webkit-keyframes buttonLeftRotatingLeft { 
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(180deg); transform: rotate(180deg); }
}

@keyframes buttonLeftRotatingLeft { 
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(180deg); transform: rotate(180deg); }
}

@-webkit-keyframes buttonLeftRotatingRight {
  from { -webkit-transform: rotate(180deg); transform: rotate(180deg); }
  to { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
}

@keyframes buttonLeftRotatingRight {
  from { -webkit-transform: rotate(180deg); transform: rotate(180deg); }
  to { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
}

@-webkit-keyframes buttonRightRotatingRight {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(180deg); transform: rotate(180deg); }
}

@keyframes buttonRightRotatingRight {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(180deg); transform: rotate(180deg); }
}

@-webkit-keyframes buttonRightRotatingLeft {
  from { -webkit-transform: rotate(180deg); transform: rotate(180deg); }
  to { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
}

@keyframes buttonRightRotatingLeft {
  from { -webkit-transform: rotate(180deg); transform: rotate(180deg); }
  to { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
}

.buttonLeftMovingLeft {
  -webkit-animation-name: buttonLeftMovingLeft, buttonLeftRotatingLeft;
          animation-name: buttonLeftMovingLeft, buttonLeftRotatingLeft;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: easeInOutExpo;
          animation-timing-function: easeInOutExpo;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.buttonLeftMovingRight {
