}

.buttonRightMovingLeft {
  -webkit-animation-name: buttonRightMovingLeft, buttonRightRotatingLeft;
          animation-name: buttonRightMovingLeft, buttonRightRotatingLeft;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: easeInOutExpo;
          animation-timing-function: easeInOutExpo;
