.visual {
  position: relative;
  width: 100%;
  height: 100%;
}

.chart {
  display: inline-block;
  background-color: whitesmoke;
  width: 70%;
  height: 100%;
  overflow: hidden;
  border: 1px solid gray;
}

.points {
  display: inline-block;
  position: relative;
  background-color: whitesmoke;
  width: 30%;
  height: 100%;
  border: 1px solid gray;
}

.newPointForm {
  position: absolute;
  top: 0px;
  width: 100%;
  display: block;
  overflow: hidden;
}

.pointsList {
  position: absolute;
  display: block;
  top: 40px;
  width: 100%;
  height: 90%;
  overflow: scroll;
}

.svg-chart {
  width: 100%;
  height: 100%;
}

.line {
  stroke: steelblue;
  stroke-width: 2;
  fill: none;
}

.axis path {
  stroke: black;
}

.circle .text {
  fill: red;
  font-size: 11px;
  font-family: Sans-Serif, Arial;
}



